// __tests__/screens/Dashboard/components/SimpleMetrics.test.js
import React from 'react';
import { render, waitFor } from '@testing-library/react-native';
import SimpleMetrics from '../../../../src/screens/Dashboard/components/SimpleMetrics';
import { ThemeContext } from '../../../../src/contexts/ThemeContext';
import useActivityMetrics from '../../../../src/hooks/useActivityMetrics';
import useDailyWords from '../../../../src/hooks/useDailyWords';

// Mock des hooks
jest.mock('../../../../src/hooks/useActivityMetrics');
jest.mock('../../../../src/hooks/useDailyWords');

describe('SimpleMetrics', () => {
  const mockThemeContext = {
    colors: {
      surface: '#FFFFFF',
      text: '#1F2937',
      textSecondary: '#6B7280'
    }
  };

  const renderWithTheme = (component, themeValue = mockThemeContext) => {
    return render(
      <ThemeContext.Provider value={themeValue}>
        {component}
      </ThemeContext.Provider>
    );
  };

  beforeEach(() => {
    jest.clearAllMocks();
  });

  describe('√âtat de chargement', () => {
    it('devrait afficher l\'indicateur de chargement', () => {
      useActivityMetrics.mockReturnValue({
        currentStreak: 0,
        streakTrend: null,
        formattedTime: '0min'
      });

      useDailyWords.mockReturnValue({
        wordsToday: 0,
        isLoading: true
      });

      const { getByText } = renderWithTheme(<SimpleMetrics />);

      expect(getByText('Chargement des m√©triques...')).toBeTruthy();
    });
  });

  describe('√âtat vide (pas d\'activit√©)', () => {
    beforeEach(() => {
      useActivityMetrics.mockReturnValue({
        currentStreak: 0,
        streakTrend: null,
        formattedTime: '0min'
      });

      useDailyWords.mockReturnValue({
        wordsToday: 0,
        isLoading: false
      });
    });

    it('devrait afficher l\'√©tat vide quand aucune activit√©', () => {
      const { getByText } = renderWithTheme(<SimpleMetrics />);

      expect(getByText('üéØ')).toBeTruthy();
      expect(getByText('Commencez votre premi√®re session !')).toBeTruthy();
      expect(getByText('Vos statistiques appara√Ætront ici')).toBeTruthy();
    });

    it('ne devrait pas afficher les m√©triques dans l\'√©tat vide', () => {
      const { queryByText } = renderWithTheme(<SimpleMetrics />);

      expect(queryByText('üìä Aujourd\'hui')).toBeNull();
      expect(queryByText('üî•')).toBeNull();
      expect(queryByText('üìö')).toBeNull();
      expect(queryByText('‚è±Ô∏è')).toBeNull();
    });
  });

  describe('Affichage des m√©triques avec activit√©', () => {
    beforeEach(() => {
      useActivityMetrics.mockReturnValue({
        currentStreak: 5,
        streakTrend: '+1 üî•',
        formattedTime: '15min'
      });

      useDailyWords.mockReturnValue({
        wordsToday: 12,
        isLoading: false
      });
    });

    it('devrait afficher le titre de section', () => {
      const { getByText } = renderWithTheme(<SimpleMetrics />);

      expect(getByText('üìä Aujourd\'hui')).toBeTruthy();
    });

    it('devrait afficher toutes les m√©triques', () => {
      const { getByText } = renderWithTheme(<SimpleMetrics />);

      // Ic√¥nes
      expect(getByText('üî•')).toBeTruthy();
      expect(getByText('üìö')).toBeTruthy();
      expect(getByText('‚è±Ô∏è')).toBeTruthy();

      // Valeurs
      expect(getByText('5')).toBeTruthy();
      expect(getByText('12')).toBeTruthy();
      expect(getByText('15min')).toBeTruthy();

      // Labels
      expect(getByText('Jours de suite')).toBeTruthy();
      expect(getByText('Mots aujourd\'hui')).toBeTruthy();
      expect(getByText('Temps aujourd\'hui')).toBeTruthy();
    });

    it('devrait afficher le trend pour le streak', () => {
      const { getByText } = renderWithTheme(<SimpleMetrics />);

      expect(getByText('+1 üî•')).toBeTruthy();
    });
  });

  describe('Gestion des valeurs nulles/undefined', () => {
    it('devrait g√©rer les valeurs nulles gracieusement', () => {
      useActivityMetrics.mockReturnValue({
        currentStreak: null,
        streakTrend: null,
        formattedTime: null
      });

      useDailyWords.mockReturnValue({
        wordsToday: null,
        isLoading: false
      });

      const { getByText } = renderWithTheme(<SimpleMetrics />);

      // Devrait afficher l'√©tat vide
      expect(getByText('Commencez votre premi√®re session !')).toBeTruthy();
    });

    it('devrait afficher 0 pour les valeurs undefined', () => {
      useActivityMetrics.mockReturnValue({
        currentStreak: undefined,
        streakTrend: null,
        formattedTime: '5min' // Au moins une valeur pour √©viter l'√©tat vide
      });

      useDailyWords.mockReturnValue({
        wordsToday: undefined,
        isLoading: false
      });

      const { getByText } = renderWithTheme(<SimpleMetrics />);

      expect(getByText('0')).toBeTruthy(); // currentStreak
      expect(getByText('5min')).toBeTruthy();
    });
  });

  describe('Diff√©rents types de trends', () => {
    it('devrait afficher un trend positif', () => {
      useActivityMetrics.mockReturnValue({
        currentStreak: 3,
        streakTrend: '+2 üí™',
        formattedTime: '10min'
      });

      useDailyWords.mockReturnValue({
        wordsToday: 8,
        isLoading: false
      });

      const { getByText } = renderWithTheme(<SimpleMetrics />);

      expect(getByText('+2 üí™')).toBeTruthy();
    });

    it('devrait afficher un trend n√©gatif', () => {
      useActivityMetrics.mockReturnValue({
        currentStreak: 1,
        streakTrend: '-1',
        formattedTime: '5min'
      });

      useDailyWords.mockReturnValue({
        wordsToday: 3,
        isLoading: false
      });

      const { getByText } = renderWithTheme(<SimpleMetrics />);

      expect(getByText('-1')).toBeTruthy();
    });

    it('devrait afficher un trend neutre', () => {
      useActivityMetrics.mockReturnValue({
        currentStreak: 2,
        streakTrend: '=',
        formattedTime: '8min'
      });

      useDailyWords.mockReturnValue({
        wordsToday: 5,
        isLoading: false
      });

      const { getByText } = renderWithTheme(<SimpleMetrics />);

      expect(getByText('=')).toBeTruthy();
    });

    it('ne devrait pas afficher de trend pour les mots et le temps', () => {
      useActivityMetrics.mockReturnValue({
        currentStreak: 4,
        streakTrend: '+1 üî•',
        formattedTime: '20min'
      });

      useDailyWords.mockReturnValue({
        wordsToday: 15,
        isLoading: false
      });

      const { queryByText } = renderWithTheme(<SimpleMetrics />);

      // Seul le trend du streak devrait √™tre visible
      expect(queryByText('+1 üî•')).toBeTruthy();
      
      // Pas de trends pour les autres m√©triques
      const trendElements = queryByText(/\+|\-/);
      expect(trendElements).toBeTruthy(); // Seulement le trend du streak
    });
  });

  describe('Couleurs et th√®me', () => {
    it('devrait utiliser la couleur d\'accent personnalis√©e', () => {
      useActivityMetrics.mockReturnValue({
        currentStreak: 0,
        streakTrend: null,
        formattedTime: '0min'
      });

      useDailyWords.mockReturnValue({
        wordsToday: 0,
        isLoading: true
      });

      const customColor = '#FF5722';
      const { getByText } = renderWithTheme(
        <SimpleMetrics accentColor={customColor} />
      );

      expect(getByText('Chargement des m√©triques...')).toBeTruthy();
    });

    it('devrait utiliser la couleur par d√©faut si aucune couleur n\'est sp√©cifi√©e', () => {
      useActivityMetrics.mockReturnValue({
        currentStreak: 2,
        streakTrend: null,
        formattedTime: '10min'
      });

      useDailyWords.mockReturnValue({
        wordsToday: 5,
        isLoading: false
      });

      const { getByText } = renderWithTheme(<SimpleMetrics />);

      expect(getByText('üìä Aujourd\'hui')).toBeTruthy();
    });

    it('devrait fonctionner sans contexte de th√®me', () => {
      useActivityMetrics.mockReturnValue({
        currentStreak: 1,
        streakTrend: null,
        formattedTime: '5min'
      });

      useDailyWords.mockReturnValue({
        wordsToday: 3,
        isLoading: false
      });

      const { getByText } = render(<SimpleMetrics />);

      expect(getByText('üìä Aujourd\'hui')).toBeTruthy();
      expect(getByText('1')).toBeTruthy();
      expect(getByText('3')).toBeTruthy();
      expect(getByText('5min')).toBeTruthy();
    });
  });

  describe('Cas limites', () => {
    it('devrait afficher les m√©triques m√™me avec une seule valeur non-nulle', () => {
      useActivityMetrics.mockReturnValue({
        currentStreak: 0,
        streakTrend: null,
        formattedTime: '0min'
      });

      useDailyWords.mockReturnValue({
        wordsToday: 1, // Seule valeur non-nulle
        isLoading: false
      });

      const { getByText } = renderWithTheme(<SimpleMetrics />);

      expect(getByText('üìä Aujourd\'hui')).toBeTruthy();
      expect(getByText('1')).toBeTruthy();
    });

    it('devrait g√©rer les temps format√©s diff√©remment', () => {
      useActivityMetrics.mockReturnValue({
        currentStreak: 1,
        streakTrend: null,
        formattedTime: '1h 30min'
      });

      useDailyWords.mockReturnValue({
        wordsToday: 25,
        isLoading: false
      });

      const { getByText } = renderWithTheme(<SimpleMetrics />);

      expect(getByText('1h 30min')).toBeTruthy();
    });

    it('devrait g√©rer les grandes valeurs', () => {
      useActivityMetrics.mockReturnValue({
        currentStreak: 365,
        streakTrend: '+1 üèÜ',
        formattedTime: '2h 45min'
      });

      useDailyWords.mockReturnValue({
        wordsToday: 100,
        isLoading: false
      });

      const { getByText } = renderWithTheme(<SimpleMetrics />);

      expect(getByText('365')).toBeTruthy();
      expect(getByText('100')).toBeTruthy();
      expect(getByText('2h 45min')).toBeTruthy();
    });
  });

  describe('Erreurs des hooks', () => {
    it('devrait g√©rer les erreurs d\'useActivityMetrics', () => {
      useActivityMetrics.mockReturnValue({
        currentStreak: 0,
        streakTrend: null,
        formattedTime: null
      });

      useDailyWords.mockReturnValue({
        wordsToday: 5,
        isLoading: false
      });

      const { getByText } = renderWithTheme(<SimpleMetrics />);

      // Devrait quand m√™me afficher les m√©triques avec les valeurs disponibles
      expect(getByText('üìä Aujourd\'hui')).toBeTruthy();
      expect(getByText('5')).toBeTruthy();
    });

    it('devrait g√©rer les erreurs d\'useDailyWords', () => {
      useActivityMetrics.mockReturnValue({
        currentStreak: 3,
        streakTrend: '+1',
        formattedTime: '15min'
      });

      useDailyWords.mockReturnValue({
        wordsToday: 0,
        isLoading: false
      });

      const { getByText } = renderWithTheme(<SimpleMetrics />);

      // Devrait afficher les m√©triques avec les valeurs disponibles
      expect(getByText('üìä Aujourd\'hui')).toBeTruthy();
      expect(getByText('3')).toBeTruthy();
      expect(getByText('15min')).toBeTruthy();
    });
  });
});